$slick-arrow-color: white !default;
$slick-dot-color: black !default;
$slick-dot-color-active: $slick-dot-color !default;
$slick-prev-character: url(/svg/arrow-left.svg) !default;
$slick-next-character: url(/svg/arrow-right.svg) !default;
$slick-opacity-default: 0.75 !default;
$slick-opacity-on-hover: 1 !default;
$slick-opacity-not-active: 0.25 !default;

.docslider {
  margin: 60px auto 0 auto;
  //max-width: 1200px

  &__name {
    font-size: 30px;
    letter-spacing: 0;
    color: #000000;
    font-weight: 400;
    font-family: $font;
    text-align: center;
    margin-top: 40px; }
  &__num {
    font-size: 15px;
    letter-spacing: 0;
    color: #2ab738;
    font-weight: 500;
    font-family: $font;
    text-align: center;
    margin-top: 10px; }

  &__box {
    margin: 10px;
    transition: $trans-main;
    &:hover {
      filter: drop-shadow(1.5px 2.598px 12px rgba(123,154,177,0.22)); }
    &.docslider__box--extend {
      padding: 40px 35px 0 35px;
      background: #fff;
      border-radius: 10px;
      height: 100%; } }

  &__img {
    overflow: hidden;
    transition: $trans-main;
    width: 210px;
    height: 270px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    margin: 0 auto;
    img {
      max-width: 100%; } }
  &__img:hover {
    filter: drop-shadow(1.5px 2.598px 12px rgba(123,154,177,0.22)); }

  // Arrows */
  .slick-prev,
  .slick-next {
    position: absolute;
    display: block;
    height: 100px;
    width: 30px;
    line-height: 0px;
    font-size: 0px;
    cursor: pointer;
    background: transparent;
    color: transparent;
    top: 50%;
    -webkit-transform: translate(0, -100%);
    -ms-transform: translate(0, -100%);
    transform: translate(0, -100%);
    padding: 0;
    border: none;
    outline: none;
    transition: $trans-main;
    &:hover { //, &:focus
      outline: none;
      background: transparent;
      color: transparent;
      filter: invert(45%) sepia(18%) saturate(5082%) hue-rotate(358deg) brightness(99%) contrast(110%);
      &:before {
        opacity: $slick-opacity-on-hover; } }


    &.slick-disabled:before {
      opacity: $slick-opacity-not-active; }

    &:before {
      height: 34px;
      width: 36px;
      display: block;
      font-size: 20px;
      content: $slick-next-character;
      line-height: 1;
      color: $slick-arrow-color;
      opacity: $slick-opacity-default;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale; } }

  .slick-next {
    right: -25px; }

  .slick-prev {
    left: -25px;
    [dir="rtl"] & {
      left: auto;
      right: -20px; }

    &:before {
      content: $slick-prev-character;
      [dir="rtl"] & {
        content: $slick-next-character; } } }

  .slick-dots {
    display: flex;
    list-style: none;
    justify-content: center;
    padding: 0;
    margin-top: 33px;
    li {
      button {
        font-size: 0;
        width: 15px;
        height: 15px;
        display: block;
        background: #fff;
        border-radius: 50%;
        border: 1px solid #000; }
      + li {
        margin-left: 10px; } }
    li.slick-active {
      button {
        background: #000; } } } }

@media screen and (min-width: 560px) {
  .docslider {
    &__img {
      width: 290px;
      height: 350px; } } }

@media screen and (min-width: 768px) {
  .docslider {
    max-width: 700px; } }

@media screen and (min-width: 990px) {
  .docslider {
    max-width: 940px; } }

@media screen and (min-width: 1200px) {
  .docslider {
    max-width: 1100px; } }

@media screen and (min-width: 1400px) {
  .docslider {
    max-width: 1200px; } }
